
'use client';

import { useState } from 'react';
import { useForm, useFieldArray } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import * as z from 'zod';
import { checkInteractions } from '@/app/actions';
import { type DetectDrugInteractionsOutput } from '@/ai/flows/detect-drug-interactions';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Form, FormControl, FormField, FormItem, FormLabel, FormMessage } from '@/components/ui/form';
import { Input } from '@/components/ui/input';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { Trash2, PlusCircle, FileScan, AlertCircle, Loader2 } from 'lucide-react';
import { useToast } from '@/hooks/use-toast';
import { InteractionCard } from './interaction-card';
import { Alert, AlertDescription, AlertTitle } from '@/components/ui/alert';
import { PrescriptionUpload } from './prescription-upload';
import { Separator } from './ui/separator';

const formSchema = z.object({
  patientName: z.string().min(1, 'Patient name is required.'),
  membershipNumber: z.string().min(1, 'Membership number is required.'),
  drugs: z
    .array(
      z.object({
        name: z.string().min(1, 'Drug name is required.'),
        rxNumber: z.string().min(1, 'RX number is required.'),
      })
    )
    .min(2, 'At least two drugs are required for an interaction check.'),
});

type FormValues = z.infer<typeof formSchema>;

export function DrugInteractionChecker() {
  const [isLoading, setIsLoading] = useState(false);
  const [results, setResults] = useState<DetectDrugInteractionsOutput | null>(null);
  const [activeTab, setActiveTab] = useState('text');
  const { toast } = useToast();

  const form = useForm<FormValues>({
    resolver: zodResolver(formSchema),
    mode: 'onBlur',
    defaultValues: {
      patientName: '',
      membershipNumber: '',
      drugs: [{ name: '', rxNumber: '' }, { name: '', rxNumber: '' }],
    },
  });

  const { fields, append, remove } = useFieldArray({
    control: form.control,
    name: 'drugs',
  });

  const onSubmit = async (data: FormValues) => {
    setIsLoading(true);
    setResults(null);
    const drugList = data.drugs.map((d) => d.name);
    const response = await checkInteractions(drugList);

    if (response && 'error' in response) {
      toast({
        variant: 'destructive',
        title: 'Error',
        description: response.error,
      });
    } else {
      setResults(response);
    }
    setIsLoading(false);
  };

  const handleDrugsParsed = (drugList: string[]) => {
    const currentPatientName = form.getValues('patientName');
    const currentMembershipNumber = form.getValues('membershipNumber');
    
    const newDrugFields = drugList.map((name) => ({ name, rxNumber: '' }));
    if (newDrugFields.length < 2) {
      newDrugFields.push({ name: '', rxNumber: '' });
      if (newDrugFields.length < 2) {
        newDrugFields.push({ name: '', rxNumber: '' });
      }
    }
    form.reset({
      patientName: currentPatientName,
      membershipNumber: currentMembershipNumber,
      drugs: newDrugFields,
    });
    setActiveTab('text');
  };

  return (
    <div className="space-y-8">
      <Card className="shadow-lg">
        <CardHeader>
          <CardTitle className="text-2xl font-headline">Drug Interaction Checker</CardTitle>
          <CardDescription>
            Enter patient and drug information to check for potential interactions.
          </CardDescription>
        </CardHeader>
        <CardContent>
          <Tabs value={activeTab} onValueChange={setActiveTab}>
            <TabsList className="grid w-full grid-cols-2 md:w-auto md:inline-grid">
              <TabsTrigger value="text">Enter Details Manually</TabsTrigger>
              <TabsTrigger value="upload">
                <FileScan className="mr-2 h-4 w-4" />
                Upload Prescription
              </TabsTrigger>
            </TabsList>
            <TabsContent value="text" className="pt-4">
              <Form {...form}>
                <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-6">
                  <h3 className="text-lg font-medium">Patient Information</h3>
                  <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                    <FormField
                      control={form.control}
                      name="patientName"
                      render={({ field }) => (
                        <FormItem>
                          <FormLabel>Patient Name</FormLabel>
                          <FormControl>
                            <Input placeholder="e.g., John Doe" {...field} />
                          </FormControl>
                          <FormMessage />
                        </FormItem>
                      )}
                    />
                    <FormField
                      control={form.control}
                      name="membershipNumber"
                      render={({ field }) => (
                        <FormItem>
                          <FormLabel>Membership Number</FormLabel>
                          <FormControl>
                            <Input placeholder="e.g., MEM12345" {...field} />
                          </FormControl>
                          <FormMessage />
                        </FormItem>
                      )}
                    />
                  </div>
                  <Separator />
                  <h3 className="text-lg font-medium">Medications</h3>
                  <div className="space-y-4">
                    {fields.map((field, index) => (
                      <div key={field.id} className="flex items-start gap-2">
                          <div className="w-20 pt-2 shrink-0 text-sm font-medium">Drug #{index + 1}</div>
                          <div className="grid grid-cols-1 md:grid-cols-2 gap-2 flex-1">
                            <FormField
                                control={form.control}
                                name={`drugs.${index}.name`}
                                render={({ field }) => (
                                    <FormItem>
                                        <FormControl>
                                            <Input placeholder="Drug Name (e.g., Lisinopril)" {...field} />
                                        </FormControl>
                                        <FormMessage/>
                                    </FormItem>
                                )}
                            />
                            <FormField
                                control={form.control}
                                name={`drugs.${index}.rxNumber`}
                                render={({ field }) => (
                                    <FormItem>
                                        <FormControl>
                                            <Input placeholder="RX Number (e.g., 1234567)" {...field} />
                                        </FormControl>
                                        <FormMessage/>
                                    </FormItem>
                                )}
                            />
                          </div>
                          <Button
                              type="button"
                              variant="ghost"
                              size="icon"
                              onClick={() => remove(index)}
                              disabled={fields.length <= 2}
                              className="text-muted-foreground hover:text-destructive"
                          >
                              <Trash2 className="h-4 w-4" />
                          </Button>
                      </div>
                    ))}
                  </div>
                  <FormMessage>{form.formState.errors.drugs?.message}</FormMessage>

                  <div className="flex flex-col gap-4 sm:flex-row sm:items-center sm:justify-between">
                    <Button
                      type="button"
                      variant="outline"
                      onClick={() => append({ name: '', rxNumber: '' })}
                      className="w-full sm:w-auto"
                    >
                      <PlusCircle className="mr-2 h-4 w-4" />
                      Add Another Drug
                    </Button>
                    <Button type="submit" disabled={isLoading} className="w-full sm:w-auto">
                      {isLoading ? (
                        <>
                          <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                          Checking...
                        </>
                      ) : (
                        'Check Interactions'
                      )}
                    </Button>
                  </div>
                </form>
              </Form>
            </TabsContent>
            <TabsContent value="upload" className="pt-4">
              <PrescriptionUpload onDrugsParsed={handleDrugsParsed} />
            </TabsContent>
          </Tabs>
        </CardContent>
      </Card>

      {isLoading && (
        <div className="flex flex-col items-center justify-center rounded-lg border border-dashed p-12 text-center">
            <Loader2 className="h-12 w-12 animate-spin text-primary" />
            <p className="mt-4 text-lg font-medium">Analyzing interactions...</p>
            <p className="text-muted-foreground">Please wait while our AI checks the drugs.</p>
        </div>
      )}

      {results && (
        <div>
          <h2 className="mb-4 text-2xl font-bold font-headline">Interaction Results</h2>
          {results.interactions && results.interactions.length > 0 ? (
            <div className="grid gap-6 md:grid-cols-1 lg:grid-cols-2">
              {results.interactions.map((interaction, index) => (
                <InteractionCard key={index} interaction={interaction} />
              ))}
            </div>
          ) : (
            <Alert className="bg-green-50 border-green-200 dark:bg-green-950 dark:border-green-800">
              <AlertCircle className="h-4 w-4 !text-green-600 dark:!text-green-400" />
              <AlertTitle className="text-green-800 dark:text-green-300">All Clear!</AlertTitle>
              <AlertDescription className="text-green-700 dark:text-green-400">
                No significant interactions were found among the specified drugs.
              </AlertDescription>
            </Alert>
          )}
        </div>
      )}
    </div>
  );
}
